.TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 2rem;
    max-width: 500px;
    background:#f7b32a;
    color: white;
    box-shadow: -20px -20px 0px 0px rgba(100,100,100,0.1);
}

.TodoList ul {
    margin-top: 2.5rem;
    list-style: none;
}

.TodoList h1{
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.TodoList h1 span {
    display: block;
    font-size: 0.9rem;
    margin-bottom: 0.7rem;
    margin-left: 3px;
    margin-top: 0.2rem;
}