@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html,
body{
  background: #2bc0e4; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2bc0e4, #eaecc6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 1.1rem;
  font-family: 'Montserrat', sans-serif;
  height: 100%
}

input{
  font-family: 'Montserrat', sans-serif;
}

button{
  font-family: 'Montserrat', sans-serif;
}



.Todo {
    display: flex;
    margin:0 -3rem 4px;
    padding: 1.1rem;
    justify-content: space-between;
    align-items: center;
    background: rgba(255,255,255,0.2);
}
.TodoItem {
    position: relative;
    transition: opacity 0.4s linear;
}

.TodoItem complete {
    opacity: 0.6;
}



.Todo-edit-form input{
    flex-grow: 1;
    border: none;
    background: #f7f1f1;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
    height: 2rem;
}

.Todo-edit-form button {
    padding: 0 1.3rem;
    border: none;
    background: #8ae4f2;
    color: white;
    font-weight: bold;
    border: 1px soild rgba(255,255,255,0.2);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.2s ease-out;
    height: 2rem;
}

.Todo-edit-form button:hover{
    background: #52cbde;
}

.Todo-buttons button{
    border: none;
    font-size: 1em;
    margin: 0.4em;
    background: none;
    -webkit-appearance: none;
    cursor: pointer;
    color: #fff;
}
.TodoItem.complete:before{
    content: "";
    position: absolute;
    top:50%;
    left:-0.5rem;
    display: block;
    width: 0%;
    height:1.5px;
    background: #fff;
    -webkit-animation: strikeitem 0.5s ease-out 0s forwards;
            animation: strikeitem 0.5s ease-out 0s forwards;
}

@-webkit-keyframes strikeitem{
    to{
        width: calc(100% + 1rem);
    }
}

@keyframes strikeitem{
    to{
        width: calc(100% + 1rem);
    }
}
.NewTodoForm{
    margin-top: 3rem;
    display:flex;
    flex-wrap: wrap;
}

.NewTodoForm input{
    flex-grow: 1;
    border: none;
    background: #f7f1f1;
    padding: 0 1.5em;
    font-size: medium;
    font-size: initial;
    height: 2rem;
}

.NewTodoForm label{
    min-width: 100%;
    margin-bottom: 0.5rem;
    font-size: 1.3rem;
}

.NewTodoForm button {
    padding: 0 1.3rem;
    border: none;
    background: #8ae4f2;
    color: white;
    font-weight: bold;
    border: 1px soild rgba(255,255,255,0.2);
    margin-left: 5px;
    cursor: pointer;
    transition: background 0.2s ease-out;
    height: 2rem;
}

.NewTodoForm button:hover{
    background: #52cbde;
}
.TodoList {
    margin: 4rem auto;
    padding: 2rem 3rem 2rem;
    max-width: 500px;
    background:#f7b32a;
    color: white;
    box-shadow: -20px -20px 0px 0px rgba(100,100,100,0.1);
}

.TodoList ul {
    margin-top: 2.5rem;
    list-style: none;
}

.TodoList h1{
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.TodoList h1 span {
    display: block;
    font-size: 0.9rem;
    margin-bottom: 0.7rem;
    margin-left: 3px;
    margin-top: 0.2rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

